
import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({
  name: 'comFormWrapper'
})
export default class comFormWrapper extends Vue {
  @Prop({ type: Boolean, default: false }) loading!: boolean
  // 
  @Prop({ type: Boolean, default: true }) isNeedFooter!: boolean
  // 
  @Prop({ type: String, default: ' ' }) cancelBtnText!: string
  // 
  @Prop({ type: String, default: ' ' }) determineBtnText!: string
  // 
  @Prop({ type: Boolean, default: true }) showCancelBtn!: boolean
  // 
  @Prop({ type: Boolean, default: true }) showDetermineBtn!: boolean
  // 
  @Prop({ type: Boolean, default: false }) cancelBtnDisabled!: boolean
  // 
  @Prop({ type: Boolean, default: false }) determineBtnDisabled!: boolean

  // 
  @Prop({ type: Boolean, default: false }) multi!: boolean
  //  multi  true 
  @Prop({ type: Number }) page!: number
  //  multi  true 
  @Prop({ type: Number }) pages!: number

  @Prop({ type: Object }) bodyStyle!: object

  @Prop({ type: Object }) contentStyle!: object

  //  
  get isShowDetermineBtn() {
    if (this.multi) {
      return this.showDetermineBtn && this.page === this.pages
    }

    return this.showDetermineBtn
  }

  // 
  get isShowPrevBtn() {
    return this.multi && this.page > 0
  }

  // 
  get isShowNextBtn() {
    return this.multi && this.page !== this.pages
  }

  submit(): void {
    this.$emit('submit')
  }

  prev() {
    this.$emit('prev')
  }

  next() {
    this.$emit('next')
  }
}
