// 
export const checkValueType = (value: any, type: string): boolean => Object.prototype.toString.call(value) === type

/**
 * 
 */
export const isArray = (value: any): boolean => checkValueType(value, '[object Array]')

/**
 * 
 */
export const isObject = (value: any): boolean => checkValueType(value, '[object Object]')

/**
 * 
 */
export const isString = (value: any): boolean => checkValueType(value, '[object String]')

/**
 * 
 */
export const isNumber = (value: any): boolean => checkValueType(value, '[object Number]')

/**
 * 
 */
export const isEmpty = (value: any): boolean => {
  //  
  if (isObject(value) && JSON.stringify(value) === '{}') {
    return true
  }

  //  
  if (isArray(value) && !value.length) {
    return true
  }

  //  
  if (isString(value) && (!value || value === 'null' || value === 'undefined')) {
    return true
  }

  //  
  if (isNumber(value)) {
    return !String(value)
  }

  if (!value) {
    return true
  }

  return false
}
